@import "./utils/_variables.scss";

.card {
  display: flex;
  flex-direction: column;
  width: 166px;
  border: solid #f3f3f3 1px;
  border-radius: 5px;
  padding: 32px 16px 16px;
  margin-right: $card-margin-right;

  &:nth-child(4n) {
    margin-right: 0;
  }
  
  &:nth-last-child(-n + 4) {
    margin-top: 46px;
  }
  

  /* #region price */
  &__price {
    display: flex;
    justify-content: space-between;
    line-height: 18px;
    margin-bottom: 16px;
  }

  &__price_text {
    font-size: 12px;
    font-weight: 400;
    color: #616070;
    align-self: center;
  }

  &__price_amount {
    // font-family: Roboto, sans-serif;
    font-style: bold;
    font-size: 16px;
    font-weight: 700;
    color: #060b35;
  }
  /* #endregion */

  /* #region button */
  &__button {
    display: flex;
    width: 166px;
    height: 40px;
    background-color: #00acdc;
    border-style: none;
    border-radius: 5px;
    align-items: center;
    justify-content: center;
    // font-family: Roboto, sans-serif;
    font-style: bold;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    text-decoration: none;
    align-self: center;
    line-height: 16px;
  }

  &__button:hover {
    box-sizing: border-box;
    background-color: #fff;
    border: solid #00acdc 1px;
    color: #00acdc;
  }
  /* #endregion */

  &__laptop_photo {
    height: 134px;
    margin-bottom: 40px;
    align-self: center;
  }

  &__produkt_description {
    font-size: 12px;
    font-weight: 500;
    color: #060b35;
    margin-bottom: 4px;
    line-height: 18px;
  }

  &__produkt_code {
    height: 14px;
    margin-bottom: 16px;
    // font-family: Roboto, sans-serif;
    font-size: 10px;
    font-weight: 400;
    color: #616070;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews_value {
    display: flex;
    font-size: 10px;
    font-weight: 400;
    color: #060b35;
    line-height: 14px;
  }
}
